import Config from "../../config";
import httpService from "../../httpService";

const defaultState = {
  score: []
};
const getters = {
  SCORING_LISTS: state => {
    return state.score;
  }
};
const mutations = {
  SET_SCORING_LIST: (state, payload) => {
    state.score = payload;
  }
};
const actions = {
  FETCH_SCORING_LIST: async context => {
    httpService.get(`${Config.apiRoot}/scoring_list`, function(status, data) {
      context.commit("SET_SCORING_LIST", data);
    });
  }
};

export default {
  state: defaultState,
  getters,
  mutations,
  actions
};
