//resources/js/plugins/mixin.js
import Vue from "vue";

Vue.mixin({
  methods: {
    sampleFunction() {
      alert("Global Functions");
    },
    sleep(ms) {
      // pause for ms milliseconds
      return new Promise((resolve) => setTimeout(resolve, ms));
    },
    computeRisk: (score) => {
      // LOW RISK	85-100
      // MOD RISK	84-74
      // HIGH RISK	75-0

      if (score === -1 || score === "" || score === null) {
        return "";
      } else if (score <= 75) {
        return "High";
      } else if (score <= 84) {
        return "Moderate";
      } else {
        return "Low";
      }
    },
    computeProviderScore: (score, rows) => {
      return Math.round(((10 * rows - score) / (10 * rows)) * 100);
    },
    computePracticeScore: (deductions, totalRows) => {
      // using deductions to compute the score first
      // 8.10.2020 - (Sum of all Audit Scores)/Total Number of Providers
      // (only count providers that have no charges)
      if (totalRows == 0) {
        return "";
      } else {
        const totalScore = totalRows * 100;
        //      return (((totalScore - deductions) / totalScore) * 100).toFixed(0);
        return ((totalScore - deductions) / totalRows).toFixed(0);
      }
    },
    computePracticeScore_1: (score, totalRows) => {
      // using the score of the provider
      if (totalRows == 0 || isNaN(score)) {
        return "";
      } else {
        return (score / totalRows).toFixed(0);
      }
    },
    viewBlob: (blob) => {
      // https://stackoverflow.com/questions/16245767/creating-a-blob-from-a-base64-string-in-javascript
      var url = `data:application/pdf;base64,${Buffer.from(blob).toString("base64")}`;

      fetch(url)
        .then((res) => res.blob())
        .then((blobInfo) => {
          const blobUrl = URL.createObjectURL(blobInfo);

          const win = window.open(blobUrl, "_blank");
          win.focus();
        });
    },
  },
});
