import Config from "../../config";
import httpService from "../../httpService";
import authentication from "../../authentication";

const defaultState = {
  rolesNeeded: {
    a: "user",
    b: "administrator",
  },
  currentUser: {},
  allUsers: [],
};
const getters = {
  CURRENT_USER: (state) => {
    return state.currentUser;
  },
  ALL_USERS: (state) => {
    return state.allUsers;
  },
};
const mutations = {
  SET_CURRENT_USER: (state, payload) => {
    // set the role
    // FIXME - need to set role, but allUsers not populated yet.
    // payload.role = state.allUsers.find(el => {
    //   return el.ad_username == payload.unique_name;
    // }).role;

    state.currentUser = payload.allUsers;
  },
  SET_ALL_USERS: (state, payload) => {
    state.allUsers = payload.allUsers;
    state.currentUser = payload.currentUser;
  },
};
const actions = {
  FETCH_USERS: (context) => {
    httpService.get(`${Config.apiRoot}/users`, function(status, data) {
      // really messing this up here
      let data1 = [];
      data1.allUsers = data.sort((a, b) => {
        if (a.fullname < b.fullname) {
          return -1;
        }
        if (a.fullname > b.fullname) {
          return 1;
        }

        // names must be equal
        return 0;
      });

      let currentUser = authentication.getUserProfile();
      const tmpUser = data.find((el) => {
        return el.ad_username && el.ad_username.toLowerCase() == currentUser.unique_name.toLowerCase();
      });

      // currentUser.role = data.find(el => {
      //   return el.ad_username == currentUser.unique_name;
      // }).role;
      currentUser.role = tmpUser.role;
      currentUser.id = tmpUser.id;

      data1.currentUser = currentUser;

      context.commit("SET_ALL_USERS", data1);
    });
  },
  SET_CURRENT_USER: ({ commit }, payload) => {
    commit("SET_CURRENT_USER", payload);
  },
};

export default {
  state: defaultState,
  getters,
  mutations,
  actions,
};
